function buildItemLink(projectId, languageCodename, itemId) {
  return "https://app.kontent.ai/goto/edit-item/project/".concat(projectId, "/variant-codename/").concat(languageCodename, "/item/").concat(itemId);
}
export function buildElementLink(projectId, languageCodename, itemId, elementCodename) {
  return "".concat(buildItemLink(projectId, languageCodename, itemId), "/element/").concat(elementCodename);
}
export function buildComponentElementLink(projectId, languageCodename, itemId, contentComponentId, componentElementCodename) {
  return "".concat(buildItemLink(projectId, languageCodename, itemId), "/component/").concat(contentComponentId, "/element/").concat(componentElementCodename);
}
export function buildKontentLink(data) {
  if (data.contentComponentId) {
    return buildComponentElementLink(data.projectId, data.languageCodename, data.itemId, data.contentComponentId, data.elementCodename);
  } else {
    return buildElementLink(data.projectId, data.languageCodename, data.itemId, data.elementCodename);
  }
}